import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {FieldBlocProps} from './fieldbloc';

export class SelectField extends React.Component<FieldBlocProps, any> {
    render(){
        var keys = Object.keys(this.props.schema.values);
        var lng = this.props.currentLanguage;
        var options = keys.map((k)=>{
            var item = this.props.schema.values[k];
            var label = item[lng];
            return <option key={k} value={k}>{label}</option>
        });
        var label = this.props.schema.labels[this.props.currentLanguage];
        return <div>
            <label htmlFor={ this.props.schema.key }>{label}</label>
            <select id={ this.props.schema.key } name={ this.props.schema.key }>
                {options}
            </select>
        </div>
    }
}


