import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {FieldBlocProps} from './fieldbloc';

export class FieldNotSupported extends React.Component<FieldBlocProps, any> {
    render(){
        return <span className="notsupported">
            not supported { this.props.schema.type }
        </span>
    }
}


