import * as _ from 'lodash';
import * as React from 'react';
import {PropTypes} from 'react';
import * as ReactDOM from 'react-dom';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import {position} from './popover.positioning';
import "./popover.less";

export interface PopoverProps {
    show: boolean;
    className? : string;
    addClassNameToBody? : string;
    parent: any;
    onhide: () => void;
    placement?: string;
    targetPlacement?: string;
    useOverlay?: boolean;
    offset? : number;
    borderOffset? : number;
    transitionName? : string
    transitionAppearTimeout?:number;
    transitionEnterTimeout?:number;
    transitionLeaveTimeout?:number;
}

export class Popover extends React.Component<PopoverProps, any> {
    wrapperNode: HTMLElement;

    constructor(props) {
        super(props);
        this.state = {
            dismiss : false
        }
    }

    hide(arg){
        this.setState({ dismiss : true });
        if (this.props.onhide){
            this.props.onhide();
        }
    }

    renderWrapper() {
        if (!this.wrapperNode) {
            //TODO check si parent...
            this.wrapperNode = document.createElement("DIV");
            this.wrapperNode.className = "popover-wrapper";
            document.body.appendChild(this.wrapperNode);
        }

        var content;

        if (!this.state.dismiss){
            content = <PopoverWrapper
                parent={this.props.parent}
                children={this.props.children}
                hidePopover={ this.hide.bind(this) }
                useOverlay={this.props.useOverlay}
                placement={this.props.placement}
                targetPlacement={this.props.placement}
                offset={this.props.offset}
                borderOffset={this.props.borderOffset}
                />;
        }
        
        ReactDOM.render(
            <ReactCSSTransitionGroup component="div"
                className={ "popover-shell " + (this.props.useOverlay !== false ? 'with-overlay ' : '') + (this.props.className || '') }
                transitionName={ this.props.transitionName || "popovertransition" } 
                transitionAppear={true}
                transitionAppearTimeout={this.props.transitionAppearTimeout || 400}
                transitionEnterTimeout={this.props.transitionEnterTimeout || 400} 
                transitionLeaveTimeout={this.props.transitionLeaveTimeout || 300}>
                {content}
                </ReactCSSTransitionGroup>,
            this.wrapperNode);
    }

    destroyWrapper() {
        if (this.wrapperNode) {
            ReactDOM.unmountComponentAtNode(this.wrapperNode);
            this.wrapperNode.parentElement.removeChild(this.wrapperNode);
            this.wrapperNode = null;
        }
    }

    componentDidMount() {
        if (this.props.show) {
            this.renderWrapper();
        } else {
            this.destroyWrapper();
        }
    }

    componentWillUnmount() {
        this.destroyWrapper();
    }

    componentWillReceiveProps(nextprops) {
        if (nextprops.show){
            this.setState({ dismiss : false });
        }
        else if (this.wrapperNode){
            this.setState({ dismiss : true });            
        }
    }

    componentDidUpdate() {
        if (this.props.addClassNameToBody){
            if (this.props.show){
                document.body.classList.add(this.props.addClassNameToBody);
            }else{
                document.body.classList.remove(this.props.addClassNameToBody);
            }
        }

        if (this.props.show || this.wrapperNode) {
            this.renderWrapper();
        } else {
            this.destroyWrapper();
        }

        if (this.state.dismiss){
            var wrapper = this.wrapperNode;
            this.wrapperNode = null;
            setTimeout(function() {
                ReactDOM.unmountComponentAtNode(wrapper);
                wrapper.parentElement.removeChild(wrapper);
            }, 500);
        }
    }

    render() {
        return <div className="popover-placeholder" style={{ display: "none" }}></div>;
    }
}

export interface PopoverWrapperProps {
    parent: any;
    children: any;
    hidePopover: (arg) => void;
    useOverlay: boolean;
    placement: string;
    targetPlacement: string;
    offset : number;
    borderOffset : number;
}

export class PopoverWrapper extends React.Component<PopoverWrapperProps, any> {
    tether: any;
    onresize: any;
    constructor(props) {
        super(props);
        this.state = {
            elementstyle: null,
            visible: false
        }
    }

    componentDidMount() {
        this.refresh();
        this.onresize = () => { 
            requestAnimationFrame(() => {
                this.refresh();
            });
        }
        window.addEventListener("resize", this.onresize);
    }

    refresh(){
        var node = ReactDOM.findDOMNode(this);
        var popover = node.querySelector(".popover-content") as HTMLElement;
        var target = this.props.parent;        
        if (typeof target == "string")
            target = document.querySelector(target) as HTMLElement;

        if (target) {

            this.setState({
                elementstyle: position(popover, target, {
                    placement : this.props.placement ? this.props.placement.split(' ') : ['top', 'center'],
                    targetPlacement : this.props.targetPlacement ? this.props.targetPlacement.split(' ') : ['bottom', 'center'],
                    offset : this.props.offset != undefined ? this.props.offset : 10,
                    borderOffset : this.props.borderOffset != undefined ? this.props.borderOffset : 10
                })
            });
        }
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.onresize);
        if (this.tether) {
            this.tether.destroy();
            this.tether.remove();
        }
    }

    render() {
        return <div className="popover-wrapper-content">
            <div className="popover-overlay" onMouseDown={ (arg) => this.props.hidePopover(arg)  } onTouchStart={ (arg) => this.props.hidePopover(arg)  }>
            </div>
            
            <div className="popover-container" onMouseDown={ (arg) => this.props.hidePopover(arg)  } onTouchStart={ (arg) => this.props.hidePopover(arg)  }>
                <div className="popover-content" style={ this.state.elementstyle }>
                    <div className="popover-modal" onClick={ (arg) => { arg.preventDefault(); arg.stopPropagation() } } onMouseDown={ (arg) => arg.stopPropagation()  } onTouchStart={ (arg) => arg.stopPropagation()  }>
                        { this.props.children }
                    </div>
                </div>
            </div>
        </div>;
    }
}


